import { getDefaultOptions, request } from 'api/helpers';

const resource = 'api/acc-locations';

export const apiAccLocationGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiAccLocationPost = async (serviceUrl, accLocation) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: accLocation ? JSON.stringify(accLocation) : null,
  };
  return request(url, options);
};

export const apiAccLocationPut = async (serviceUrl, accLocation) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: accLocation ? JSON.stringify(accLocation) : null,
  };
  return request(url, options);
};

export const apiAccLocationDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
